import '../styles/Ticorporate.css';
import ImageCarousel from './ImageCarousel';

const Ticorporate = () => {
  return (
    <div className="ticorporateTextContainer">
      <div className="greyTextBox">
        <h2>Ticorporate</h2>

        <p>
          Ticorporate on Jyväskylän ammattikorkeakoulun opiskelijoille
          tarkoitettu yrityssimulaatio, jossa opiskelijat luovat sovelluksia ja
          pelejä pienissä ryhmissä ketterän ohjelmistokehityksen menetelmiä
          hyödyntäen.
        </p>

        <p>
          Olin mukana vuoden 2022 syksyllä järjestetyssä Ticorporate Demo Lab
          -kurssin yhdeksännessä toteutuksesssa. Tällä sivulla kerron
          kokemuksistani ja opeistani tuolta neljän kuukauden ajalta.
        </p>
      </div>
      <h3>Roolit</h3>
      <p>
        Minun ryhmässäni oli yhteensä neljä henkilöä. Meillä jokaisella oli omat
        roolimme, jotka ohjasivat toimintaamme projektin aikana. Osa rooleista
        liittyi Scrumiin, käyttämäämme projektinhallinnan viitekehykseen.
      </p>
      <p>Minun roolini olivat:</p>
      <ul>
        <li className="starBullet">Frontend-ohjelmoija</li>

        <li className="starBullet">Product Owner</li>

        <li className="starBullet">UI/UX-suunnittelija</li>
      </ul>
      <p>
        Hieman alempana käyn läpi kokemuksiani ja oppimistani jokaiseen rooliin
        liittyen.
      </p>
      <div className="greyTextBox">
        <h3>Opintosuunnitelma</h3>

        <p>
          Ennen Ticorporaten tuotantovaiheen alkua kirjoitin itselleni
          opintosuunnitelman, jossa kävin läpi kaiken mitä haluaisin oppia
          kurssin aikana, ja mitkä olisivat omat henkilökohtaiset tavoitteeni
          siihen liittyen.
        </p>

        <p>Tavoitteinani oli muun muassa:</p>

        <ul>
          <li className="starBullet">Ohjelmointitaitojeni syventäminen.</li>

          <li className="starBullet">Scrumin ja sen roolien ymmärtäminen.</li>

          <li className="starBullet">
            Kerätä kokemusta tulevaisuuden töitä varten.
          </li>
        </ul>
      </div>

      <h3>Frontend</h3>

      <p>
        Loin ryhmäni kanssa mobiilisovelluksen nimeltä Brita, johon voi helposti
        kerätä omat suosikkireseptinsä. Sovelluksessa on paljon erilaisia
        ruoanlaittoa ja reseptien organisointia helpottavia ominaisuuksia. Alla
        on kuvankaappauksia sovelluksesta.
      </p>

      <div className="imageCarousel">
        <ImageCarousel />
      </div>

      <p>
        Sovelluksen käyttöliittymän luomisessa käytimme{' '}
        <a
          href="https://reactjs.org/"
          target="_blank"
          rel="noreferrer noopener"
        >
          Reactia
        </a>
        . Päädyimme valitsemaan Reactin lähinnä siitä syystä, että minä olin
        juuri käynyt Helsingin yliopiston{' '}
        <a
          href="https://fullstackopen.com/"
          target="_blank"
          rel="noreferrer noopener"
        >
          Full Stack Open
        </a>{' '}
        -verkkokurssin, josta olin oppinut Reactia ja tykästynyt siihen.
      </p>

      <p>
        Full Stack Openista opin monet Reactin perusteista, mutta Ticorporaten
        aikana pääsin syventämään osaamistani.
      </p>

      <div className="greyTextBox">
        <h4>React: Plussat ja miinukset</h4>

        <ul>
          <li className="plusBullet">Helppo ja nopea</li>
          <li className="plusBullet">Paljon ohjeita saatavilla</li>
          <li className="plusBullet">Lukuisia valmiita kirjastoja</li>
          <li className="plusBullet">Komponenttien uudelleenkäytettävyys</li>

          <li className="minusBullet">
            Antaa paljon vapauksia, mikä voi johtaa myös ongelmiin
          </li>
        </ul>

        <p>
          Myös yksi asia, joka sai minut kyseenalaistamaan Reactin valinnan oli
          se, että tiimimme toisella frontend-devaajalla ei ollut siitä alussa
          kokemusta, mikä vaikeutti hänen työtään.
        </p>
      </div>

      <h4>Sovelluksessa käytettyjä kirjastoja:</h4>
      <ul>
        <li className="starBullet">
          <a
            href="https://www.framer.com/motion/"
            target="_blank"
            rel="noreferrer noopener"
          >
            Framer Motion
          </a>{' '}
          animaatioiden luomiseen.
        </li>

        <li className="starBullet">
          <a
            href="https://axios-http.com/"
            target="_blank"
            rel="noreferrer noopener"
          >
            Axios
          </a>{' '}
          backend-pyyntöjen tekemiseen.
        </li>

        <li className="starBullet">
          <a
            href="https://www.npmjs.com/package/react-image-crop"
            target="_blank"
            rel="noreferrer noopener"
          >
            React Image Crop
          </a>{' '}
          kuvien rajaamiseen reseptejä skannatessa (alla lisätietoja).
        </li>

        <li className="starBullet">
          <a
            href="https://www.npmjs.com/package/react-image-crop"
            target="_blank"
            rel="noreferrer noopener"
          >
            React Image File Resizer
          </a>{' '}
          käyttäjien lisäämien kuvien pienentämiseen.
        </li>

        <li className="starBullet">
          <a
            href="https://www.npmjs.com/package/react-share"
            target="_blank"
            rel="noreferrer noopener"
          >
            React Share
          </a>{' '}
          reseptien ja listojen jakamiseen eri sovelluksien kautta.
        </li>
      </ul>

      <h4>Esimerkkejä luomuksistani</h4>

      <p>
        Projektin aikana sain luotua sovellukseemme monia ominaisuuksia, joista
        olen ylpeä.
      </p>

      <p>
        Yksi hyvä esimerkki on kirjoitettujen reseptien kopioiminen kuvista.
        Ominaisuuden ansiosta käyttäjät voivat tallettaa sovellukseen
        suosikkireseptinsä esimerkiksi omista keittokirjoistaan.
      </p>

      <div className="greyTextBox">
        <div className="textAndGifContainer">
          <div className="textAndGifContainerText">
            <h4>Näin ominaisuus toimii:</h4>

            <p>
              Käyttäjä ottaa laitteellaan kuvan tai valitsee valmiin kuvan
              galleriastaan. Kuva tallettuu tilaan ja sovellus siirtyy
              eteenpäin.
            </p>

            <p>
              <a
                href="https://github.com/jamktiko/reseptipankki/blob/main/reseptipankki/src/components/PhotoRecipe.js"
                target="_blank"
                rel="noreferrer noopener"
              >
                Ominaisuuden komponentin koodi Githubissa.
              </a>
            </p>

            <p>
              Käyttäjä rajaa kuvasta yksitellen reseptin nimen, ainekset ja
              ohjeet. Jokainen rajattu kuva laitetaan omaan tilaansa.
            </p>

            <p>
              <a
                href="https://github.com/jamktiko/reseptipankki/blob/main/reseptipankki/src/components/PhotoRecipeCropper.js"
                target="_blank"
                rel="noreferrer noopener"
              >
                Rajaukseen käytettävän komponentin koodi.
              </a>
            </p>

            <p>
              Rajausten jälkeen sovellus ottaa talteen rajatuissa kuvissa olevan
              tekstin käyttäen AWS Rekognitionia.
            </p>

            <p>
              Kuvasta rajattua reseptin nimeä ei tarvitse muokata, mutta
              aineksia ja ohjeita tarvitsee:
            </p>

            <p>
              Jokaisesta ainesosarivistä on eroteltava aineksen nimi, määrä ja
              määrän yksikkö, mikä tehdään tässä{' '}
              <a
                href="https://github.com/jamktiko/reseptipankki/blob/main/reseptipankki/src/hooks/ingredientsConverter.js"
                target="_blank"
                rel="noreferrer noopener"
              >
                hookissa
              </a>
              . Tämä oli haastava ominaisuus, eikä se toimi täydellisesti, mutta
              olen tyytyväinen.
            </p>

            <p>
              Reseptin ohjeista poistetaan tarpeettomat rivinvaihdot tässä{' '}
              <a
                href="https://github.com/jamktiko/reseptipankki/blob/main/reseptipankki/src/hooks/instructionsConverter.js"
                target="_blank"
                rel="noreferrer noopener"
              >
                hookissa
              </a>
              .
            </p>

            <p>
              Kaikki oikeassa muodossa oleva teksti menee sovelluksen
              reseptilomakkeeseen. Siinä reseptiä voi vielä muokata ja täydentää
              ennen sen tallettamista.
            </p>
          </div>

          <img
            src={require('../images/scan.gif')}
            alt="Gif jossa resepti kopioidaan kuvasta."
          />
        </div>
      </div>

      <p>
        Muita sovelluksen ominaisuuksia joista olen erityisen tyytyväinen ovat
        esimerkiksi listat ja kaikki niihin liittyvät toiminnallisuudet, sekä
        ateriasuunnittelija.
      </p>

      <p>
        Sovelluksen kaikkien komponenttien tiedostoja pääsee katsomaan{' '}
        <a
          href="https://github.com/jamktiko/reseptipankki/tree/main/reseptipankki/src/components"
          target="_blank"
          rel="noreferrer noopener"
        >
          tästä
        </a>
        .
      </p>

      <p>
        Täydellisesti luotu sovellus Brita ei kuitenkaan ole. Siihen jäi
        muutamia bugeja ja monia muita asioita, jotka olisi voinut tehdä
        paremmin. Aikamme ei riittänyt niiden korjaamiseen.
      </p>

      <p>
        Suurin frontend-ohjelmoijana tekemäni moka oli Reduxin unohtaminen.{' '}
        <a
          href="https://redux.js.org/"
          target="_blank"
          rel="noreferrer noopener"
        >
          Redux
        </a>{' '}
        on tilanhallintaan tarkoitettu kirjasto, jolla luotuja tiloja voi
        käyttää tavallisia tiloja laajemmin. Sen puute johti monessa paikkaa
        sovelluksemme koodin huonoon spagettimaiseen rakenteeseen.
      </p>

      <h4>Yhteenveto Reactista</h4>

      <p>
        Kaiken kaikkiaan olen tyytyväinen, että teimme sovelluksen Reactilla,
        sillä en usko että olisimme saaneet kaikkia ominaisuuksia valmiiksi jos
        olisimme tehneet sovelluksen esimerkiksi Angularilla.
      </p>

      <p>
        Brita-sovellusta pääset kokeilemaan selaimessasi{' '}
        <a
          href="https://master.ddudu42yaerab.amplifyapp.com/"
          target="_blank"
          rel="noreferrer noopener"
        >
          tästä
        </a>
        . Se on PWA-sovellus, joten se toimii hyvin myös mobiilissa, ja on
        asennettavissa laitteellesi.
      </p>

      <p>
        Sovelluksen Github-repositorion löydät{' '}
        <a
          href="https://github.com/jamktiko/reseptipankki"
          target="_blank"
          rel="noreferrer noopener"
        >
          täältä
        </a>
        .
      </p>

      <h3>Product Owner</h3>

      <p>Ensimmäinen sivuroolini oli olla projektin Product Owner.</p>

      <p>
        Ryhmäni Product Ownerina olin vastuussa siitä, että työmme vei
        sovellustamme kohti visiotamme. Product Ownerin vastuisiin kuului myös
        backlogimme pitäminen selkeänä ja hyvin organisoituna. Myös asiakkaiden
        kanssa kommunikointi ja heidän intresseistänsä vastaaminen kuuluisivat
        PO:n töihin, mutta koska meidän projektillamme ei varsinaisesti ollut
        ketään asiakasta, sitä ei voida laskea saavutuksiini.
      </p>

      <p>
        Minun ei tarvinnut huolehtia siitä, etteivätkö ryhmäni muut jäsenet
        olisi olleet perillä visiostamme. Heti projektin alussa jouduimme
        määrittämään sovellustamme uusiksi melko radikaalisti, minkä seurauksena
        ikään kuin loimme uuden vision yhdessä. Tämän ansiosta kaikki olimme
        tuotantovaiheen alkaessa hyvin perillä siitä, millainen valmiin
        tuotteemme tulisi olla.
      </p>

      <p>
        Tästä syystä koenkin, että tärkein tehtäväni Product Ownerina oli
        vastata backlogistamme. Projektinhallintajärjestelmänämme toimi{' '}
        <a
          href="https://www.zenhub.com/"
          target="_blank"
          rel="noreferrer noopener"
        >
          Zenhub
        </a>
        , josta minulle jäi erittäin hyvä vaikutelma.
      </p>

      <p>
        Zenhubiin kirjoitimme lukuisia taskeja, joissa jokaisessa kuvataan jokin
        sovelluksen ominaisuus käyttäjätarinoineen, sen valmiiksi toteamiseen
        vaadittavat kriteerit (Acceptance Criteria) sekä muutama geneerinen
        vaatimus, jotka sen tulee vielä täyttää ennen kuin ominaisuus on ns.
        julkaisukelpoinen (Definition of Done).
      </p>

      <img
        src={require('../images/task.jpg')}
        alt="Kuva Zenhubin taskista nimeltä Reseptin poistaminen"
        className="zenhubTaskImage"
      />

      <p>
        Kun näin sen tarpeelliseksi, pidin itsekseni ns. Backlog
        Refinement-sessioita, joissa kävin läpi backlogiamme Zenhubissa ja
        varmistin, että jokainen taski siellä oli kirjoitettu selkeästi ja{' '}
        <a
          href="https://en.wikipedia.org/wiki/INVEST_(mnemonic)"
          target="_blank"
          rel="noreferrer noopener"
        >
          INVEST
        </a>
        -kriteerit täyttäen, ja että niistä löytyi kaikki tarvittavat
        vaatimukset. Pyrin pitämään taskit myös tärkeysjärjestyksessä.
      </p>

      <p>
        Jokainen sprinttimme alkoi Sprint Planningilla, jossa päätimme sprintin
        tavoitteista. Product Ownerina annoin niissä omat perustellut
        mielipiteeni siitä, mitä kaikkea meidän kannattaisi sprintin aikana
        pyrkiä tekemään.
      </p>

      <p>
        Sprinttiemme suunnitteluja voidaan pitää erittäin onnistuneina, sillä
        saavutimme kaikki tavoitteemme jokaisella sprintillä, ja projektin
        loppuessa koko backlogimme oli tyhjä, joten olimme päässeet
        tavoitteeseemme aikataulussa.
      </p>

      <h3>UI/UX-suunnittelija</h3>

      <p>
        Toinen sivuroolini projektissamme oli olla sovelluksemme
        UI/UX-suunnittelija.
      </p>

      <p>
        Tästä roolista huolimatta en voi ottaa täyttä kunniaa sovelluksemme
        käyttöliittymästä tai sen suunnittelusta. Projektin
        konseptointivaiheessa, ennen kuin rooleja oli edes jaettu, loimme
        ryhmäni kanssa yhdessä prototyypin sovelluksestamme käyttäen{' '}
        <a
          href="https://www.figma.com/"
          target="_blank"
          rel="noreferrer noopener"
        >
          Figma
        </a>
        -palvelua. Prototyypin ulkoasu säilyi lähes muuttumattomana valmiiseen
        sovellukseen asti.
      </p>

      <h4>Prototyyppi (vasen) ja valmis sovellus (oikea)</h4>

      <img
        src={require('../images/compare.png')}
        alt="Kuva Zenhubin taskista nimeltä Reseptin poistaminen"
        className="comparisonImage"
      />

      <p>
        Monet yksityiskohdat ovat kuitenkin minun suunnittelemiani. Koska
        designerin roolin lisäksi vastasin sovelluksen frontendistä ja olin
        vielä Product Ownerikin, sain muilta melko vapaat kädet tehdä asiat
        omalla tyylilläni, ottaen kuitenkin tarvittaessa heidän mielipiteensä
        huomioon. Kirjoitin itse muun muassa valtaosan sovelluksen CSS:stä.
      </p>

      <p>
        Näkyvin esimerkki UI-työstäni projektissa on sovelluksen värimaailman
        luominen. Toteutin tämän ensin luomalla Figmassa joukon erilaisia
        väriyhdistelmiä jotka sopisivat sovellukseen, ja sitten äänestimme
        niistä ryhmäläisten ja muutaman ulkopuolisenkin kesken.
      </p>

      <p>
        Olisin mielelläni käyttänyt sovelluksen UI/UX-suunnitteluun enemmän
        aikaa, mutta se ei ollut prioriteeteissämme kovin korkealla. Valmiin
        sovelluksen ulkonäkö on yksinkertainen ja selkeä, mutta siitä olisi
        mielestäni saanut vielä paremman näköisen.
      </p>

      <div className="greyTextBox">
        <h3>Yhteenveto</h3>

        <p>
          Mielestäni saavutin kaikki opintosuunnitelmassani itselleni asettamat
          tavoitteet aika hyvin. Opin paljon uutta lisää Reactista ja
          Javascriptistä ylipäänsä.
        </p>

        <p>
          Koenkin että itsevarmuuteni ohjelmoijana kasvoi projektin aikana.
          Sovellusta suunnitellessa päätimme lisätä siihen monia sellaisia
          ominaisuuksia, joista en tiennyt ollenkaan, kuinka ne tehtäisiin
          käytännössä tai olisiko niiden toteuttaminen edes mahdollista minun
          taidoillani. Huomattuani, että pystyin ylittämään kaikki vastaan
          tulleet vaikeudet, voin ottaa uusia haasteita vastaan entistä
          varmemmin.
        </p>

        <p>
          Ticorporate myös vahvisti käsitystäni siitä, että nimenomaan frontend
          on sitä alaa, jossa olen parhaimmillani, ja jonka parissa haluan tehdä
          töitä tulevaisuudessakin.
        </p>

        <p>
          Suurimmaksi vahvuuteni ovat mielestäni kykyni oppia uusia asioita
          nopeasti, sekä kykyni ratkaista ongelmia.
        </p>

        <p>
          Opittavaakin minulla on vielä. Yksi Ticorporaten aikana muutamaan
          otteeseen esiin noussut asia on se, että saatan lähteä toteuttamaan
          ensimmäistä mieleeni tullutta ratkaisuehdotusta miettimättä tarpeeksi,
          voisiko olla jotain parempia ratkaisuja.
        </p>

        <p>
          Kaiken kaikkiaan Ticorporate oli todella opettavainen ja silmiä avaava
          kokemus. Lisäksi tiimini kanssa työskentely oli pääosin todella
          mukavaa, minkä ansiosta Ticorporatesta jää kokemuksen lisäksi monia
          hyviä muistoja. 👍
        </p>
      </div>
    </div>
  );
};

export default Ticorporate;
