import 'react-responsive-carousel/lib/styles/carousel.min.css';
import { Carousel } from 'react-responsive-carousel';
import { useState } from 'react';

// Komponentti, jossa määritetään kuvakaruselli.
const ImageCarousel = () => {
  // Tieto siitä, monesko slide on näkyvissä.
  const [currentSlide, setCurrentSlide] = useState(1);
  // Tieto siitä, ollaanko menossa takaisin alkuun.
  const [goingToStart, setGoingToStart] = useState(false);
  // Näitä kahta käytetään hidastamaan karusellin liikettä kun se palaa alkuun.

  // Funktio joka käsittelee edellä määritellyt tilat karusellin liikkuessa.
  const handleChange = (id: number) => {
    setCurrentSlide(id + 1);
    if (id + 1 === 16) {
      setGoingToStart(true);
    } else {
      setTimeout(() => {
        setGoingToStart(false);
      }, 500);
    }
  };

  return (
    <div>
      <Carousel
        autoPlay={true}
        interval={5000}
        showStatus={false}
        showIndicators={false}
        centerMode={true}
        centerSlidePercentage={33.333}
        showThumbs={false}
        transitionTime={currentSlide === 1 && goingToStart ? 4000 : 500}
        infiniteLoop={true}
        onChange={handleChange}
        stopOnHover={false}
        swipeable={false}
      >
        <div>
          <img
            src={require(`../images/screenshot1.jpg`)}
            alt="Kuva sovelluksen etusivusta"
          />
        </div>

        <div>
          <img
            src={require(`../images/screenshot2.jpg`)}
            alt="Kuva sovelluksen sivuvalikosta"
          />
        </div>

        <div>
          <img
            src={require(`../images/screenshot3.jpg`)}
            alt="Kuva sovelluksen hakusivulta"
          />
        </div>

        <div>
          <img
            src={require(`../images/screenshot5.jpg`)}
            alt="Kuva reseptistä sovelluksessa"
          />
        </div>

        <div>
          <img
            src={require(`../images/screenshot6.jpg`)}
            alt="Kuva reseptin valikosta"
          />
        </div>

        <div>
          <img
            src={require(`../images/screenshot7.jpg`)}
            alt="Kuva reseptin jakamisikkunasta"
          />
        </div>

        <div>
          <img
            src={require(`../images/screenshot8.jpg`)}
            alt="Kuva reseptin rajaamisesta käyttäjän ottamasta kuvasta"
          />
        </div>

        <div>
          <img
            src={require(`../images/screenshot9.jpg`)}
            alt="Kuva reseptin kopioimisesta linkin avulla"
          />
        </div>

        <div>
          <img
            src={require(`../images/screenshot10.jpg`)}
            alt="Kuva sovelluksen reseptinlisäyslomakkeesta"
          />
        </div>

        <div>
          <img
            src={require(`../images/screenshot11.jpg`)}
            alt="Kuva sovelluksen listasivulta"
          />
        </div>

        <div>
          <img
            src={require(`../images/screenshot12.jpg`)}
            alt="Kuva sovelluksen käyttäjän tekemästä listasta"
          />
        </div>

        <div>
          <img
            src={require(`../images/screenshot13.jpg`)}
            alt="Kuva reseptin poistamisesta listalta"
          />
        </div>

        <div>
          <img
            src={require(`../images/screenshot14.jpg`)}
            alt="Kuva sovelluksen ateriakalenterista"
          />
        </div>

        <div>
          <img
            src={require(`../images/screenshot15.jpg`)}
            alt="Kuva sovelluksen ostoslistasta"
          />
        </div>

        <div>
          <img
            src={require(`../images/screenshot16.jpg`)}
            alt="Kuva sovelluksen käyttäjän omista resepteistä"
          />
        </div>

        <div>
          <img
            src={require(`../images/screenshot17.jpg`)}
            alt="Kuva sovelluksen käyttäjäsivulta"
          />
        </div>
      </Carousel>
    </div>
  );
};

export default ImageCarousel;
