import { Link } from 'react-router-dom';
import '../styles/FrontPage.css';

const FrontPage = () => {
  return (
    <div>
      <div className="textAndGraphics">
        <div>
          <div className="mobileGraphicsContainer">
            <img
              className="frontPageImage"
              src={require('../images/img.jpg')}
              alt="Kuva minusta."
            />

            <div className="circleContainer">
              <div className="circle" />

              <div className="circle" />

              <div className="circle" />

              <div className="circle" />

              <div className="circle" />
            </div>
          </div>

          <h2>Hei!</h2>

          <p>
            Olen Ville Kuusela, 22-vuotias tietojenkäsittelyn opiskelija
            Jyväskylästä. Erikoisalaani on frontend, mutta hallitsen koko Full
            Stackista vähintään perusteet.
          </p>

          <p>
            Tällä sivustolla voit tutustua minuun itseeni, sekä muutamiin
            opintojeni aikana luomiini <Link to="/projektit">projekteihin</Link>
            . Löydät myös <Link to="/yhteystiedot">yhteystietoni</Link>, jos
            haluat ottaa yhteyttä.
          </p>

          <h3>Opinnot</h3>

          <p>
            Aloitin tietojenkäsittelyn opiskelun Jyväskylän
            ammattikorkeakoulussa syksyllä 2020. Sitä edeltävä kokemukseni
            ohjelmoinnista oli hyvin vähäistä, mutta lyhyessä ajassa olen
            oppinut valtavasti.
          </p>

          <p>
            Tästä kertoo muun muassa erinomaiset arvosanani, sekä
            opintomenestyksestäni minulle myönnetty stipendi. Stipendin
            myöntämisperusteena oli lukuvuoden paras keskiarvo alalla.
          </p>

          <p>Tavoitteenani on valmistua joulukuussa 2023.</p>

          <p>
            Ennen JAMK:issa aloittamista valmistuin Schildtin lukiosta erittäin
            hyvin paperein.
          </p>

          <h3>Taidot</h3>

          <p>
            Ominta alaani on frontend-ohjelmointi. Minulla on kokemusta Reactin,
            Angularin sekä Svelten käytöstä. HTML, CSS ja JavaScript/TypeScript
            sujuvat minulta erinomaisen hyvin ottaen huomioon kuinka lyhyen
            aikaa olen vasta opiskellut niitä.
          </p>

          <p>
            Myös backendin puolelta osaan monet asiat vähintään perustasolla.
          </p>

          <p>
            Lisäksi olen nopea oppimaan uutta, mistä todisteena on
            opintomenestykseni. Tulen hyvin toimeen kaikenlaisten ihmisten
            kanssa.
          </p>
        </div>

        <div className="wideGraphicsContainer">
          <div className="graphics">
            <img
              className="frontPageImage wideScreenImage"
              src={require('../images/img.jpg')}
              alt="Kuva minusta."
            />

            <div className="circleContainer wideScreenCircles">
              <div className="circle" />

              <div className="circle" />

              <div className="circle" />

              <div className="circle" />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default FrontPage;
