import { useState } from 'react';
import '../styles/Navigation.css';
import { Link } from 'react-router-dom';
import { TbMenu2 } from 'react-icons/tb';
import SideMenu from './SideMenu';
import { AnimatePresence } from 'framer-motion';

const Navigation = () => {
  // Tieto siitä, onko sivuvalikko auki vai ei.
  const [sideMenuOpen, toggleSideMenuOpen] = useState(false);

  return (
    <div>
      <div className="navigationBar">
        <div className="navigationBarContent">
          <Link to="/">
            <h1 onClick={() => toggleSideMenuOpen(false)}>Ville Kuusela</h1>
          </Link>

          <div className="links">
            <Link to="/">Minusta</Link>

            <Link to="/projektit">Projektit</Link>

            <Link to="/ticorporate">Ticorporate</Link>

            <Link to="/yhteystiedot">Yhteystiedot</Link>
          </div>

          <div
            className="sideMenuIcon"
            onClick={() => toggleSideMenuOpen(!sideMenuOpen)}
          >
            <TbMenu2 />
          </div>
        </div>
      </div>

      <AnimatePresence>
        {sideMenuOpen && <SideMenu toggleSideMenuOpen={toggleSideMenuOpen} />}
      </AnimatePresence>
    </div>
  );
};

export default Navigation;
