import '../styles/SideMenu.css';
import { motion } from 'framer-motion';
import { Link } from 'react-router-dom';
import { TbChevronRight } from 'react-icons/tb';

interface Props {
  toggleSideMenuOpen(value: boolean): any;
}

const SideMenu = ({ toggleSideMenuOpen }: Props) => {
  const closeMenu = () => {
    toggleSideMenuOpen(false);
  };

  return (
    <motion.div
      key="sideMenu"
      initial={{ x: 1000 }} // Näkymän sijainti ennen animaatiota
      animate={{ x: 1 }} // Näkymän sijainti animaation jälkeen
      transition={{ duration: 0.35, ease: 'easeOut' }} // Kesto ja pehmennys
      exit={{ x: 1000 }} // Sijainti johon näkymä menee kadotessaan
      className="sideMenu"
    >
      <div className="sideMenuContent">
        <div className="sideMenuLinks">
          <Link to="/" onClick={() => closeMenu()}>
            <span>Minusta</span>
            <TbChevronRight />
          </Link>

          <Link to="/projektit" onClick={() => closeMenu()}>
            <span>Projektit</span>
            <TbChevronRight />
          </Link>

          <Link to="/ticorporate" onClick={() => closeMenu()}>
            <span>Ticorporate</span>
            <TbChevronRight />
          </Link>

          <Link to="/yhteystiedot" onClick={() => closeMenu()}>
            <span>Yhteystiedot</span>
            <TbChevronRight />
          </Link>
        </div>
      </div>
    </motion.div>
  );
};

export default SideMenu;
