import { useState, useEffect } from 'react';
import { TbBrandGithub, TbPlayerPlay } from 'react-icons/tb';
import '../styles/ProjectPage.css';

interface Project {
  name: string;
  description: string[];
  images: string[];
  imageTitles: string[];
  github: string;
  link: string;
}

const ProjectPage = () => {
  const [data, setData] = useState<Project>();

  const projectId = window.location.href.substring(
    window.location.href.lastIndexOf('/') + 1
  );

  useEffect(() => {
    try {
      const projectData = require(`./../projects/${projectId}.json`);
      setData(projectData);
    } catch (error) {
      console.error(error);
    }
  }, [projectId]);

  return (
    <div>
      {data && (
        <div>
          <h2>{data?.name}</h2>

          {data?.description.map((item, index) => {
            return <p key={index}>{item}</p>;
          })}

          {data?.link && (
            <a
              href={data?.link}
              target="_blank"
              rel="noreferrer noopener"
              className="contactBox"
            >
              <TbPlayerPlay />{' '}
              <span className="underlineText">
                Kokeile sovellusta selaimessasi
              </span>
            </a>
          )}

          <a
            href={data?.github}
            target="_blank"
            rel="noreferrer noopener"
            className="contactBox"
          >
            <TbBrandGithub />{' '}
            <span className="underlineText">
              Sovelluksen Github-repositorio
            </span>
          </a>

          <div className="projectImagesBox">
            <h3>Kuvia sovelluksesta:</h3>

            {data?.images.map((item, index) => {
              try {
                return (
                  <div key={index}>
                    {data?.imageTitles && <h4>{data?.imageTitles[index]}</h4>}

                    <img
                      src={require(`../images/project${projectId}/${item}`)}
                      alt="Kuva sovelluksesta."
                      className="projectImage"
                    />
                  </div>
                );
              } catch {
                return <div key={index} />;
              }
            })}
          </div>
        </div>
      )}
    </div>
  );
};

export default ProjectPage;
